<template>
    <div class="searchTop">
        <div class="searchNav">
            <!-- 返回主界面 -->
            <span class="iconfont icon-xitongfanhui" @click="$router.go(-1)"></span>
            <!-- 输入需要查找的关键字 -->
            <input type="text" 
                v-model="searchWord" 
                :placeholder="placeholder" 
                @keydown.enter="setKeyWord"
            >
        </div>
        <div v-if="!searchWord" class="history">
            <span class="title">历史:</span>
            <span class="historyItem" 
                v-for="(item,i) in keywordList" 
                :key="i" 
                @click="historySearch(item)"
            >
                {{item}}
            </span>
        </div>
        <div v-else class="listBody">
            <div class="list" v-for="(item,index) in result.data.result.songs" :key="index"  @click="setPlayIndex(index)">
                <div class="album">
                    <div class="listNum">{{index+1}}</div>
                    <div class="songItem">
                        <span class="listName">{{item.name}}</span>
                        <div class="tags">
                            <span class="albumName">{{item.al.name}}-{{item.ar[0].name}}</span>
                        </div>
                    </div>
                </div>
                <div class="con">
                    <span class="iconfont icon-bofang"></span>
                    <span class="iconfont icon-qita"></span>
                </div>        
            </div>
        </div>
    </div>
</template>
<script>
import { getCurrentInstance, onBeforeMount, reactive, toRefs } from 'vue';
import {searchMusic} from '@/assets/apifuntion.js';
import { mapMutations,useStore } from 'vuex';
export default{
    name:'searchTop',
    methods:{
        ...mapMutations(['setPlayIndex'])
    },
    setup(){
        let store=useStore();
        let data=reactive({
            searchWord:'',
            placeholder:'123',
            keywordList:[],
            result:{
                data:{
                    result:{
                        songs:[{
                            name:'',
                            ar:[{
                                name:''
                            }],
                            al:{
                                name:''
                            }
                        }]
                    }
                }
            }
        });
        let {proxy}=getCurrentInstance();
        onBeforeMount(()=>{
            data.keywordList=localStorage.keywordList?JSON.parse(localStorage.keywordList):[];
        })
        let setKeyWord=async()=>{
            console.log('data:',data);
            if(data.searchWord.trim()===''){
                return;
            }
            data.keywordList.push(data.searchWord);
            // 把搜索记录去重
            data.keywordList=Array.from(new Set(data.keywordList));
            if(data.keywordList.length>10){
                data.keywordList=data.keywordList.slice(1,11);
            }
            localStorage.keywordList=JSON.stringify(data.keywordList);
            // 请求查找数据
            let result=await searchMusic(data.searchWord);
            console.log('333',result);
            data.result=result;
            store.commit('setPlayList',result.data.result.songs)
            store.dispatch('reqMusic',result.data.result.songs[0])
        }
        let historySearch=(item)=>{
            data.searchWord=item;
            proxy.setKeyWord()
        }
        return{
            ...toRefs(data),
            proxy,store,
            setKeyWord,historySearch
        }
    }
}
</script>
<style lang="less" scoped>
    ol {
        list-style: decimal;
    }
    .searchTop{
        width: 100%;
        height: auto;
        padding: 0.5rem 0.2rem;
        .searchNav{
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .iconfont{
                font-size: 0.5rem;
            }
            input{
                flex:1;
                height: 0.8rem;
                padding: 0.3rem;
                background-color: #eee;
                border: none;
                border-bottom: 1px solid #000;
                border-radius: 0.4rem;
                font-size: 0.5rem;
                color: #000;
            }
        }
        .history{
            width: 100%;
            height: auto;
            padding: 0.2rem;
            display: flex;
            flex-wrap: wrap;
            .title{
                font-size: 0.3rem;
                font-weight: 900;
                border-radius: 50%;
                margin: 0.3rem 0.4rem 0 0;
            }
            .historyItem{
                font-weight: 500;
                border-radius: 0.3rem;
                background-color: #eee;
                font-size: 0.3rem;
                padding: 0.1rem 0.2rem;
                margin: 0.2rem 0.4rem 0 0;
            }
        }
        .listBody{
            position: fixed;
            top: 2rem;
            bottom: 0;
            left: 0.05rem;
            width: 7.5rem;
            overflow: scroll;
            background-color: #fff;
            .list{
                width: 100%;
                height: 0.8rem;
                display: flex;        
                justify-content: space-between;
                align-items: center;
                border-top: 0.5px solid #ccc;
                .album{
                    display: flex;
                    .listNum{
                        width: 0.5rem;
                    }
                    .songItem{
                        display: flex;
                        flex-direction: column;
                        .listName{
                            font-size: 0.3rem;
                            font-weight: 900;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            display: -webkit-box;
                            -webkit-line-clamp: 1 ;
                            -webkit-box-orient: vertical;
                        }
                        .tags{
                            display: flex;
                            align-items: center;
                            .listTag{
                                font-size: 0.1rem;
                                background-color: orange;
                                line-height: 0.20rem;
                                color: #fff;
                                margin-right: 0.1rem;
                            }
                            .albumName{
                                font-size: 0.2rem;
                                color: #999;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                display: -webkit-box;
                                -webkit-line-clamp: 1 ;
                                -webkit-box-orient: vertical;
                            }
                        }
                    }
                }  
                .con{
                    .iconfont{
                        font-size: 0.5rem;
                    }
                }
                
            }    
        }
    }
</style>